
.content {
	max-width: 993px;
	height: 637px;
	margin: auto;
	font-family: arial;
	color: #666666;
  position: relative;
}
.content__title {
	font-size: 35px;
	font-weight: bold;
	padding-left: 4.5%;
	padding-bottom: 43px;
	padding-top: 36px;
	letter-spacing: 1.7px;
}
.content__logo {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 80px;
  height: 80px;
  position: absolute;
  right: 8%;
  top: 20px;
}
.content__shudle-month {
	position: relative;
}
.content__shudle-item {
	width: 22%;
	text-align: left;
	font-size: 19px;
	float: left;
	padding-top: 24px;
	padding-left: 3%;
	line-height: 36px;
}
.item_type {
}
.content__shudle-item-notice {
}
.content__shudle-month-title{
	width: 75%;
	text-align: center;
	font-size: 18px;
	padding-top: 8px;
	padding-bottom: 5px;
	background-color: #d4d4d4;
	float: left;
}
.content__shudle-month-prev{
	top: 16px;
	left: 28%;
	width: 20px;
	height: 3px;
	margin: 0 auto;
	background: #fff;
	display: inline-block;
	position: absolute;
	background-color: #666666;
	cursor: pointer;
}
.content__shudle-month-prev:before{
	content: "";
	width: 11px;
	height: 3px;
	margin: 0 auto;
	background: #fff;
	background-color: #666666;
	position: absolute;
	-webkit-transform: rotateZ(45deg);
	        transform: rotateZ(45deg);
	top: 4px;
}
.content__shudle-month-prev:after{
	content: "";
	width: 11px;
	height: 3px;
	margin: 0 auto;
	background: #fff;
	background-color: #666666;
	position: absolute;
	-webkit-transform: rotateZ(-45deg);
	        transform: rotateZ(-45deg);
	top: -4px;
}
.content__shudle-month-next{
	top: 16px;
	right: 6%;
	width: 20px;
	height: 3px;
	margin: 0 auto;
	background: #fff;
	display: inline-block;
	position: absolute;
	background-color: #666666;
	cursor: pointer;
}
.content__shudle-month-next:before{
	content: "";
	width: 11px;
	height: 3px;
	margin: 0 auto;
	background: #fff;
	background-color: #666666;
	position: absolute;
	-webkit-transform: rotateZ(-45deg);
	        transform: rotateZ(-45deg);
	top: 4px;
	right: 0px;
}
.content__shudle-month-next:after{
	content: "";
	width: 11px;
	height: 3px;
	margin: 0 auto;
	background: #fff;
	background-color: #666666;
	position: absolute;
	-webkit-transform: rotateZ(45deg);
	        transform: rotateZ(45deg);
	top: -4px;
	right: 0px;
}
.content__shudle-month-next-area{
  top: 4px;
  right: 6%;
  width: 25px;
  height: 25px;
  margin: 0 auto;
  display: inline-block;
  position: absolute;
  cursor: pointer;
}
.content__shudle-month-prev-area{
  top: 4px;
  left: 28%;
  width: 25px;
  height: 25px;
  margin: 0 auto;
  display: inline-block;
  position: absolute;
  cursor: pointer;
}
.content__shudle-month-days {
	margin-left: 25%;
	text-align: right;
}
.content__shudle-month-day {
	display: inline-block;
	width: 19.4%;
	text-align: center;
	height: 34px;
	line-height: 30px;
}
.content__shudle-month-day-numb:after {
	content: " ";
}
.content__shudle-month-room-block {
	margin-left: 4.5%;
	margin-right: 5%;
	border: 1px solid #d4d4d4;
	height: 90px;
	margin-bottom: 7px;
}
.nostyle {
	margin-bottom: 0;
	border: 0;
	height: auto;
}
.content__shudle-table {
	float: left;
	width: 75%;
	text-align: right;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.content__shudle-month-item{
	width: 22%;
	text-align: left;
	float: left;
	padding-top: 24px;
	padding-left: 3%;
	font-size: 18px;
}
.content__shudle-item-notice{
	display: block;
	color: #a1a1a1;
	font-size: 17px;
}
.content__shudle-time{
	display: inline-block;
	width: 19.4%;
}
.content__shudle-time-zone{
	width: 92%;
	display: block;
	text-align: left;
	padding-left: 8%;
	font-size: 14px;
	height: 18px;
	line-height: 18px;
	cursor: pointer;
  color: #0c0471;
}
.content__shudle-time-zone:after {
	content: "+";
	font-size: 18px;
	margin-right: 14%;
	float: right;
	line-height: 16px;
}
.content__shudle-block-title {
  display: none;
}
.content__shudle-row {
	float: left;
	width: 50%;
}
.blockreserve {
  cursor: default;
  background-color: #e0e0e0;
  color: #828282;
  text-decoration: line-through;
  font-weight: normal;
}
.blockreserve:after {
  content: "";
}
.blocked {
  background-color: #c3c3c3;
  cursor: default;
  outline: 1px solid #e6d6d6;
  color: #ffffff;
  font-weight: normal;
}
.blocked:after {
	content: "-";
}
